fieldset {
  padding: .5rem 2rem;
}

legend {
  text-transform: uppercase;
  letter-spacing: .1rem;
  font-size: .8em;
}

input:not([type="checkbox"]):not([type="radio"]):not([type="submit"]):not([type="color"]):not([type="button"]):not([type="reset"]), select, textarea, textarea[type="text"] {
  border: 1px solid var(--color-lightGrey);
  width: 100%;
  border-radius: 4px;
  padding: .8rem 1rem;
  font-family: inherit;
  font-size: 1em;
  transition: all .2s;
  display: block;
}

input:not([type="checkbox"]):not([type="radio"]):not([type="submit"]):not([type="color"]):not([type="button"]):not([type="reset"]):not(:disabled):hover, select:hover, textarea:hover, textarea[type="text"]:hover {
  border-color: var(--color-grey);
}

input:not([type="checkbox"]):not([type="radio"]):not([type="submit"]):not([type="color"]):not([type="button"]):not([type="reset"]):focus, select:focus, textarea:focus, textarea[type="text"]:focus {
  border-color: var(--color-primary);
  -webkit-box-shadow: 0 0 1px var(--color-primary);
  box-shadow: 0 0 1px var(--color-primary);
  outline: none;
}

input.error:not([type="checkbox"]):not([type="radio"]):not([type="submit"]):not([type="color"]):not([type="button"]):not([type="reset"]), textarea.error {
  border-color: var(--color-error);
}

input.success:not([type="checkbox"]):not([type="radio"]):not([type="submit"]):not([type="color"]):not([type="button"]):not([type="reset"]), textarea.success {
  border-color: var(--color-success);
}

select {
  -webkit-appearance: none;
  background: #f3f3f6 url("data:image/svg+xml;utf8,<svg xmlns='http://www.w3.org/2000/svg' width='60' height='40' fill='%23555'><polygon points='0,0 60,0 30,40'/></svg>") 100% / 1ex no-repeat content-box border-box;
}

[type="checkbox"], [type="radio"] {
  width: 1.6rem;
  height: 1.6rem;
}

.button, [type="button"], [type="reset"], [type="submit"], button {
  color: var(--color-darkGrey);
  background: var(--color-lightGrey);
  font-size: var(--font-size);
  text-align: center;
  cursor: pointer;
  border: 1px solid #0000;
  border-radius: 4px;
  padding: 1rem 2.5rem;
  line-height: 1;
  text-decoration: none;
  transition: opacity .2s;
  display: inline-block;
  transform: scale(1);
}

.grouped {
  display: flex;
}

.grouped > :not(:last-child) {
  margin-right: 16px;
}

.grouped.gapless > * {
  border-radius: 0 !important;
  margin: 0 0 0 -1px !important;
}

.grouped.gapless > :first-child {
  border-radius: 4px 0 0 4px !important;
  margin: 0 !important;
}

.grouped.gapless > :last-child {
  border-radius: 0 4px 4px 0 !important;
}

.button + .button {
  margin-left: 1rem;
}

.button:hover, [type="button"]:hover, [type="reset"]:hover, [type="submit"]:hover, button:hover {
  opacity: .8;
}

.button:active, [type="button"]:active, [type="reset"]:active, [type="submit"]:active, button:active {
  transform: scale(.98);
}

input:disabled, button:disabled, input:disabled:hover, button:disabled:hover {
  opacity: .4;
  cursor: not-allowed;
}

.button.primary, .button.secondary, .button.dark, .button.error, .button.success, [type="submit"] {
  color: #fff;
  z-index: 1;
  background-color: #000;
  background-color: var(--color-primary);
}

.button.secondary {
  background-color: var(--color-grey);
}

.button.dark {
  background-color: var(--color-darkGrey);
}

.button.error {
  background-color: var(--color-error);
}

.button.success {
  background-color: var(--color-success);
}

.button.outline {
  border-color: var(--color-lightGrey);
  background-color: #0000;
}

.button.outline.primary {
  border-color: var(--color-primary);
  color: var(--color-primary);
}

.button.outline.secondary {
  border-color: var(--color-grey);
  color: var(--color-grey);
}

.button.outline.dark {
  border-color: var(--color-darkGrey);
  color: var(--color-darkGrey);
}

.button.clear {
  color: var(--color-primary);
  background-color: #0000;
  border-color: #0000;
}

.button.icon {
  align-items: center;
  display: inline-flex;
}

.button.icon > img {
  margin-left: 2px;
}

.button.icon-only {
  padding: 1rem;
}

::-webkit-input-placeholder {
  color: #bdbfc4;
}

::-moz-placeholder {
  color: #bdbfc4;
}

:-ms-input-placeholder {
  color: #bdbfc4;
}

::placeholder {
  color: #bdbfc4;
}

.nav {
  min-height: 5rem;
  align-items: stretch;
  display: flex;
}

.nav img {
  max-height: 3rem;
}

.nav > .container {
  display: flex;
}

.nav-center, .nav-left, .nav-right {
  flex: 1;
  display: flex;
}

.nav-left {
  justify-content: flex-start;
}

.nav-right {
  justify-content: flex-end;
}

.nav-center {
  justify-content: center;
}

@media screen and (max-width: 480px) {
  .nav, .nav > .container {
    flex-direction: column;
  }

  .nav-center, .nav-left, .nav-right {
    flex-wrap: wrap;
    justify-content: center;
  }
}

.nav a, .nav .brand {
  color: var(--color-darkGrey);
  align-items: center;
  padding: 1rem 2rem;
  text-decoration: none;
  display: flex;
}

.nav .active:not(.button) {
  color: #000;
  color: var(--color-primary);
}

.nav .brand {
  padding-top: 0;
  padding-bottom: 0;
  font-size: 1.75em;
}

.nav .brand img {
  padding-right: 1rem;
}

.nav .button {
  margin: auto 1rem;
}

.card {
  background: var(--bg-color);
  -webkit-box-shadow: 0 1px 3px var(--color-grey);
  box-shadow: 0 1px 3px var(--color-grey);
  border-radius: 4px;
  padding: 1rem 2rem;
}

.card p:last-child {
  margin: 0;
}

.card header > * {
  margin-top: 0;
  margin-bottom: 1rem;
}

.tabs {
  display: flex;
}

.tabs a {
  text-decoration: none;
}

.tabs > .dropdown > summary, .tabs > a {
  color: var(--color-darkGrey);
  border-bottom: 2px solid var(--color-lightGrey);
  text-align: center;
  flex: 0 auto;
  padding: 1rem 2rem;
}

.tabs > a.active, .tabs > a:hover {
